
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Collapse from '@/components/Collapse/Collapse'
import ShowMore from '@/components/ShowMore/showMore'
import slide from '@/components/Slide/slide'
export default{
components: {
XHeader,Collapse,ShowMore,Tab,TabItem,slide
},
mounted(){
this.params = this.$route.params;
this.getInfo();
},
data(){
return {
tabIndex: 0,
info:{},
params:{},
list: [],
noData: false,
}
},
methods: {
slide(index){
this.$refs.slide.goToPage(index);
},
getInfo(){

this.$ajax.post('',{
'MSH.1':'zbsf',
'MSH.2':"qryJXBInfo",
'SFJLID':this.params.SFJLID,//精神病随访ID
'YLJGID': this.params.YLJGID,//医疗机构ID
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res)
if(res){
this.info = res;
}
}).catch(error => {
console.log(error);

});
},
getYY(){

this.$ajax.post('',{
'MSH.1':'zbsf',
'MSH.2':"qryJXBMedical",
'SFJLID':this.params.SFJLID,//精神病随访ID
'YLJGID': this.params.YLJGID,//医疗机构ID
'token':this.$root.loginInfo.token,
}).then(res => {

if(res.length != 0){
this.list = res.yyqk;
}else{
this.noData = true;
}
}).catch(error => {
console.log(error);

});
}

},
watch: {
tabIndex: function(newPage,oldPage){
if(newPage == 1){

this.getYY();
}else if(newPage == 0){
this.noData = false;
}
},
},
}




